import { processAlerts } from "@/lib/services/alert-service"

/**
 * Funci√≥n que se ejecuta peri√≥dicamente para verificar proyectos pr√≥ximos a vencer
 * Esta funci√≥n puede ser llamada desde un cron job o al iniciar el servidor
 */
export async function runDeadlineChecker() {
	try {
		const result = await processAlerts()
		return result
	} catch (error) {
		console.error("‚ùå Error en deadline checker:", error)
		return { count: 0, alerts: [], error }
	}
}

/**
 * Ejecuta la verificaci√≥n cada X minutos
 * En producci√≥n, esto deber√≠a ejecutarse mediante un cron job externo o Vercel Cron
 */
export function startDeadlineChecker(intervalMinutes: number = 60) {
	console.log(`‚è∞ Iniciando verificador de deadlines cada ${intervalMinutes} minutos`)

	// Ejecutar inmediatamente al iniciar
	runDeadlineChecker()

	// Ejecutar peri√≥dicamente
	const intervalMs = intervalMinutes * 60 * 1000
	setInterval(() => {
		runDeadlineChecker()
	}, intervalMs)
}

